import React, { useEffect } from "react";
import ReactDOM from "react-dom";
import FunnelGraph from "funnel-graph-js";

import "./styles.css";

function App() {
  useEffect(() => {
    const data = {
      labels: [
        "Crear Usuario",
        "Verificacion SMS",
        "Perfil",
        "Info Personal o Docs",
        "Domicilio o Info Pers o Info Empresa",
        "Firma Contrato"
      ],
      subLabels: ["General", "Persona FÃ­sica", "PFAE", "Moral"],
      colors: [
        ["#711cff", "#711cff"],
        ["#1caaff", "#1caaff"],
        ["#38ff1c", "#38ff1c"],
        ["#ffe31c", "#ffe31c"]
      ],
      values: [
        [3600, 0, 0, 0],
        [2996, 0, 0, 0],
        [0, 3229, 144, 22],
        [0, 2360, 136, 22],
        [0, 2327, 65, 22],
        [0, 2125, 13, 22]
      ]
    };

    const graph = new FunnelGraph({
      container: ".App",
      gradientDirection: "horizontal",
      data: data,
      displayPercent: true,
      direction: "Horizontal",
      width: 800,
      height: 350,
      subLabelValue: "percent"
    });

    graph.draw();
  }, []);

  return <div className="App" />;
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
